body {
  font-family: "Outfit", sans-serif;
  font-size: 1.8rem;
}

a {
  text-decoration: none;
}

img {
  width: 100%;
  height: auto;
}

input:focus {
  outline-color: #007df4;
}

h1, h2, h3 {
  font-family: "Outfit", sans-serif;
  font-weight: 900;
  margin: 2rem 0;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3rem;
}

.text-center {
  text-align: center;
}

[class$=__heading] {
  font-family: "Outfit", sans-serif;
  font-weight: 900;
  margin: 2rem 0;
  margin: 6rem 0 3rem 0;
  text-align: center;
}

.auth {
  width: min(80%, 60rem);
  margin: 0 auto;
}

.form {
  display: flex;
  flex-direction: column;
}
.form__field {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}
.form__label {
  display: block;
  font-size: 1.8rem;
  color: rgba(26, 27, 21, 0.8);
  margin-top: 1.5rem;
}
.form__input {
  padding: 1rem;
  border: 1px solid #b1c7dd;
  border-radius: 0.5rem;
}
.form__submit {
  background-color: #007df4;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  font-size: 1.8rem;
  font-weight: bold;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  width: 100%;
  display: inline-block;
  border-radius: 0.8rem;
}
.form__submit:hover {
  background-color: #0063c1;
  cursor: pointer;
}
@media (min-width: 768px) {
  .form__submit {
    width: auto;
    align-self: flex-end;
  }
}
.form .action-link {
  margin: 2rem 0;
  align-self: flex-end;
}

.action-link {
  display: block;
  text-align: center;
  margin: 2rem;
  font-size: 1.8rem;
  color: #64748B;
  transition: color 0.3s;
}
.action-link span {
  color: #00498e;
}
.action-link:hover {
  color: #1E293B;
}
.action-link:hover span {
  color: #007df4;
}

.header {
  min-height: 60rem;
  background-image: url(../img/grafico.svg), url(../img/grafico.svg), linear-gradient(to right, #1a1b15, rgba(26, 27, 21, 0.5)), image-set(url(../img/header.webp) 1x, url(../img/header.jpg) 1x);
  background-size: 25rem, 25rem, cover, cover;
  background-repeat: no-repeat;
  background-position: top left, bottom right, center center, center center;
}
@media (min-width: 768px) {
  .header {
    background-size: 40rem, 40rem, cover, cover;
  }
}
.header__container {
  width: min(95%, 120rem);
  margin: 0 auto;
}
.header__nav {
  padding: 5rem 0;
  display: flex;
  justify-content: center;
  gap: 2rem;
}
@media (min-width: 768px) {
  .header__nav {
    justify-content: flex-end;
  }
}
.header__link {
  color: #FFFFFF;
  font-size: 1.4rem;
  text-transform: uppercase;
  font-weight: bold;
  transition: color 0.2s;
}
.header__link:hover {
  color: #007df4;
}
.header__content {
  padding-top: 10rem;
}
@media (min-width: 768px) {
  .header__content {
    padding-top: 5rem;
  }
}
.header__logo {
  font-size: clamp(4rem, 8vw, 8rem);
  background: linear-gradient(to right, #007df4, #00c8c2);
  background-clip: text;
  color: transparent;
  text-align: center;
}
@media (min-width: 768px) {
  .header__logo {
    text-align: left;
    display: inline-block;
  }
}
.header__text {
  font-size: 3rem;
  font-weight: bold;
  margin: 1rem 0;
  color: #FFFFFF;
  text-align: center;
  text-transform: uppercase;
}
.header__text--mode {
  font-size: 2rem;
}
@media (min-width: 768px) {
  .header__text {
    text-align: left;
  }
}
.header__button {
  background-color: #00c8c2;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  font-size: 1.8rem;
  font-weight: bold;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  width: 100%;
  display: inline-block;
  border-radius: 0.8rem;
  text-transform: uppercase;
  margin: 2rem 0;
}
.header__button:hover {
  background-color: #009591;
  cursor: pointer;
}
@media (min-width: 768px) {
  .header__button {
    width: auto;
  }
}

.bar {
  background-color: #007df4;
}
.bar__content {
  width: min(95%, 120rem);
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .bar__content {
    flex-direction: row;
    justify-content: space-between;
  }
}
.bar__logo {
  color: #FFFFFF;
  text-align: center;
  font-size: clamp(2rem, 8vw, 3rem);
}
@media (min-width: 768px) {
  .bar__logo {
    text-align: left;
    margin: 0;
  }
}
.bar__logo h2 {
  margin: 0;
}

.nav {
  display: flex;
  flex-direction: column;
}
@media (min-width: 768px) {
  .nav {
    flex-direction: row;
  }
}
.nav__link {
  color: #FFFFFF;
  font-size: 2.2rem;
  padding: 2rem;
  font-weight: bold;
  text-align: center;
}
.nav__link:hover {
  background-color: #FFFFFF;
  color: #007df4;
}